
// 部门管理
import http from '../httpConfig'
const table = 'mrbase_sys_org_levels'
//  部门列表
export const getList = (params = {}) => {
    params['~table~'] = table
    return new Promise((resolve, reject) => {
        http.post('/api/org/levels/list-department/v1', params)
            .then(res => {
                resolve(res)
            })
            .catch(err => {
                reject(err)
            })
    })
}


