<template>
  <mrc_ess_skLoaders :skLoading='skLoading'>
    <div>
      <mrc_ess_iconGroup :is_disabled="disabled"
                         :show_del='false'
                         :showEdit="hasBtn('edit-department')"
                         @handleReturn='handleReturn'
                         @handleEdit='handleEdit'
                         @handleDel='handleDel'></mrc_ess_iconGroup>
      <v-card>
        <v-card-text>
          <v-row dense>
            <!-- <v-col class="d-flex"
                   cols="12"
                   sm="6">

              <v-text-field :error-messages="$errors('params','org_level_name',requiredRule.concat(maxLength(20)))"
                            @input="$v.params.org_level_name.$touch()"
                            @blur="$v.params.org_level_name.$touch()"
                            :readonly="disabled"
                            :label="$t('label_Department_name') +'*'"
                            append-icon="mdi-longitude"
                            v-model="params.org_level_name"></v-text-field>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :error-messages="$errors('params','org_level_shortcode',requiredRule.concat(maxLength(20)))"
                            @input="$v.params.org_level_shortcode.$touch()"
                            @blur="$v.params.org_level_shortcode.$touch()"
                            :readonly="disabled"
                            append-icon="mdi-longitude"
                            :label="$t('label_Short_code') +'*'"
                            v-model="params.org_level_shortcode"></v-text-field>
            </v-col> -->
            <v-col class="d-flex"
                   cols="12"
                   sm="6">

              <mrc_ess_multiText :error-messages="$errors('params','org_level_name',requiredRule.concat(maxLength(20)))"
                                 :readonly="disabled"
                                 style="width:100%"
                                 :label="$t('label_Department_name') +'*'"
                                 v-model="params.org_level_name"
                                 :isNational="judegNational('org_level_name')"></mrc_ess_multiText>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <mrc_ess_multiText :error-messages="$errors('params','org_level_shortcode',requiredRule.concat(maxLength(20)))"
                                 :readonly="disabled"
                                 style="width:100%"
                                 :label="$t('label_Short_code') +'*'"
                                 v-model="params.org_level_shortcode"
                                 :isNational="judegNational('org_level_shortcode')"></mrc_ess_multiText>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <mrc_ess_prompt :errMsg="$errors('params','org_level_code_parent',requiredRule)"
                              :disabled='$store.state.uuid2 ? true : false || disabled'
                              :rules="false"
                              :label="$t('label_Higher_office') +'*'"
                              :chips='false'
                              @getData="params.org_level_leader_uuid=''"
                              :obj='deparentmentParent'
                              v-model="params.org_level_code_parent">
              </mrc_ess_prompt>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <mrc_ess_prompt :disabled='disabled'
                              :rules="false"
                              :label="$t('label_Department_head')"
                              :chips='false'
                              noDataText='请选择上级部门/公司'
                              :obj="leaderObj"
                              v-model="params.org_level_leader_uuid">
              </mrc_ess_prompt>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <v-text-field :error-messages="$errors('params','sequence',requiredRule.concat(between('1-99')))"
                            @input="$v.params.sequence.$touch()"
                            @blur="$v.params.sequence.$touch()"
                            :readonly="disabled"
                            :label="$t('label_The_sorting') +'*'"
                            v-model.number="params.sequence"></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions v-if="!disabled">
          <mrc_ess_submitBtn @cancel='cancel'
                             @success='success'></mrc_ess_submitBtn>
        </v-card-actions>
      </v-card>
    </div>
  </mrc_ess_skLoaders>

</template>

<script>
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
import { departmentData, addDepartment, updateDepartment, delDepartment } from '@/service/api/departmentManage'
export default {
  components: {},
  props: {},
  data () {
    return {
      skLoading: true,
      disabled: true,
      params: {
        uuid: '',
        sequence: 1,
        org_level_leader_uuid: '',
        org_level_code_parent: '',
        org_level_name: '',
        org_level_shortcode: ''
      }
    };
  },
  validations: {
    params: {
      sequence: {
        required,
        between: between(1, 99)
      },
      org_level_code_parent: {
        required
      },
      org_level_name: {
        required,
        maxLength: maxLength(20)
      },
      org_level_shortcode: {
        required,
        maxLength: maxLength(20)
      }
    }
  },
  created () { },
  async mounted () {
    if (!this.$store.state.iconstatus) {
      this.disabled = false
    }
    await this.national('mrbase_sys_org_levels')
    this.skLoading = true
    const uuid = this.$route.params.uuid || this.$store.state.uuid2
    if (uuid) {
      this.params.uuid = uuid
      const title = this.$store.getters.subPage_name
      this.$route.meta.title = title
      await this.getData()
    } else {
      this.disabled = false
      this.$route.meta.title = 'add'
    }
    this.skLoading = false
  },
  methods: {
    async getData () {
      const data = await departmentData({ uuid: this.params.uuid })
      if (data.code == 200) {
        this.params.uuid = data.data.uuid
        this.params.sequence = data.data.sequence
        this.params.org_level_leader_uuid = data.data.org_level_leader_uuid
        this.params.org_level_code_parent = data.data.org_level_code_parent
        this.params.org_level_name = data.data.org_level_name
        this.params.org_level_shortcode = data.data.org_level_shortcode
      }
    },
    cancel () {
      let content
      if (this.params.uuid) {
        content = '放弃修改'
      } else {
        content = '放弃新增'
      }
      this.$myConfirm({ content })
        .then(() => {
          this.$router.go(-1)
        }).catch(() => { })
    },
    async success () {
      this.$v.params.$touch()
      if (this.$v.params.$error) return
      if (this.params.uuid) {
        const update = await updateDepartment(this.params)
        if (update.code == 200) {
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
          this.$router.go(-1)
        }
      } else {
        const add = await addDepartment(this.params)
        if (add.code == 200) {
          this.$store.commit('newsTips', this.$t('message_added_successfully'))
          this.$router.go(-1)
        }
      }
    },
    handleReturn () {
      this.$router.go(-1)
    },
    handleEdit () {
      this.disabled = !this.disabled
    },
    handleDel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') })
        .then(async () => {
          const del = await delDepartment({ uuid: this.params.uuid })
          if (del.code == 200) {
            this.$store.commit('newsTips', '删除成功')
          }
        }).catch(() => { })
    }
  },
  computed: {
    leaderObj () {
      return { prompt_name: 'dept-leader', org_level_code: this.params.org_level_code_parent }
    },
    deparentmentParent () {
      return { prompt_name: 'level_dept', org_level_code: this.$store.state.orgLevelCode }
    }
  },
  watch: {
  },
};
</script>

<style scoped>
</style>
